body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-header {
    background-color: #7f7ff7;
    font-size: calc(10px + 2vmin);
    color: white;
    padding-top: 2em;
    padding-bottom: 2em;
    text-align: center;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  .headshot {
    border-radius: 50%;
    background-color: rgba(0,0,0,.2);
    padding: 0.2em;
  }
  
  .name {
    font-family: sans-serif;
  }
  
  .links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .link-header {
    margin-left: 1.5em;
    margin-right: 1.5em;
    margin-bottom: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .link-header-text {
    margin-bottom: 0.25em;
  }
  
  .git-logo {
    width: 40%;
    height: auto;
    border-radius: 50%;
  }
  
  .git-logo:hover {
    background:#5d5df1;
  }
  
  .linkedin-logo {
    width: 40%;
    height: auto;
    border-radius: 5%;
  }
  
  .linkedin-logo:hover {
    background:#5d5df1;
  }

  .email-logo {
    width: 40%;
    height: auto;
    border-radius: 10%;
    margin-top: 0.2em;
  }

  .email-logo:hover {
    background:#5d5df1;
  }

  .document-logo {
    width: 40%;
    height: auto;
    border-radius: 15%;
  }

  .document-logo:hover {
    background:#5d5df1;
  }
.experience {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 1em;
    padding-bottom: 1em;
    width: 60%;
}
.experience-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.music {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.music-text {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: calc(10px + 2vmin);
}
.react-tabs {
    -webkit-tap-highlight-color: transparent;
  }
  
  .react-tabs__tab-list {
    margin: 0 0 10px;
    padding: 0;
    background-color: #7f7ff7;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .react-tabs__tab {
    display: inline-block;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;
    background-color: #4040cc;
    border-radius: 4px 4px 0 0;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    color: white;
    width: 25%;
    text-align: center;
    padding-top: 1em;
    padding-bottom: 1em;
  }
  
  .react-tabs__tab--selected {
    background: #fff;
    border-color: whitesmoke;
    color: black;
    border-radius: 5px 5px 0 0;
  }
  
  .react-tabs__tab--disabled {
    color: GrayText;
    cursor: default;
  }
  
  .react-tabs__tab:focus {
    box-shadow: 0 0 5px hsl(208, 99%, 50%);
    border-color: hsl(208, 99%, 50%);
    outline: none;
  }
  
  .react-tabs__tab:focus:after {
    content: "";
    position: absolute;
    height: 5px;
    left: -4px;
    right: -4px;
    bottom: -5px;
    background: whitesmoke;
  }

  .react-tabs__tab:hover {
    background-color: #5d5df1;
  }
  
  .react-tabs__tab-panel {
    display: none;
  }
  
  .react-tabs__tab-panel--selected {
    display: block;
  }
